const {
  Marker,
  ZoomControl,
  ScaleControl,
} = CedarMaps.getReactMapboxGl();

class AddNewPropertyForm extends PureComponent {
  constructor() {
    super();
    this.state = {
      type: 0,
      applicationType: 0,
      ownerType: 0,
      area: 0,
      price: 0,
      unitPrice: 0,
      deposit: 0,
      rent: 0,
      dailyRent: 0,
      images: '',
      imagesData: '',
      toggleMoreInfo: false,
      toggleTools: false,
      toggleOthers: false,
      toggleMap: false,
      isLoading: false,
      moreInfo: {
        rooms: '',
        floors: '',
        units: '',
        floorUnits: '',
        floor: '',
        parking: '',
        age: '',
        baths: '',
        masterRooms: '',
        elevators: '',
        owners: '',
        phones: '',
        entering: '',
        irWC: '',
        engWC: '',
        warehouse: false,
        balcony: false,
        lobby: false,
        simpleIPhone: false,
        advancedIPhone: false,
        cellar: false,
        tree: false,
        store: false,
        evacuation: false,
        powerThreePhase: false,
        separatePowerCounter: false,
        separateWaterCounter: false,
        separateGasCounter: false,
        floorCover: '',
      },
      tools: {
        floorCover: '',
        wallCover: '',
        cabinet: '',
        heatCoolSystem: '',
        shiralat: '',
        moble: '',
        window: '',
        view: '',
        entranceDoor: '',
        structureType: '',
        VIPfacilities: '',
      },
      otherSelection: {
        geoLocation: '',
        outlook: '',
        direction: '',
        skylightDirection: '',
        unitType: '',
        sanadType: '',
      },
      location: {
        lat: 51.34379364705882,
        lng: 35.74109568627451,
      },
    };
    this.handleTypeSelect = this.handleTypeSelect.bind(this);
    this.toggleCollapse = this.toggleCollapse.bind(this);
    this.handleInputMoreInfoChange = this.handleInputMoreInfoChange.bind(this);
    this.handleToggleChange = this.handleToggleChange.bind(this);
    this.handleSelectType = this.handleSelectType.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleMapClick = this.handleMapClick.bind(this);
    this.handleSave = this.handleSave.bind(this);
    this.onDrop = this.onDrop.bind(this);
    this.handleNumberInputChange = this.handleNumberInputChange.bind(this);
  }

  onDrop(images, imagesData) {
    this.setState({ images, imagesData });
  }


  handleTypeSelect(index, name) {
    this.setState({ [name]: index });
    console.log(index);
    console.log(name);
  }

  handleInputChange(e) {
    const { name, value } = e.target;
    if ((name === 'area') && (value > 0)) {
      const { price } = this.state;
      this.setState({
        area: value,
        unitPrice: Math.floor(price / value),
      });
      return;
    }
    if ((name === 'price') && (value > 0)) {
      const { area } = this.state;
      this.setState({
        price: value,
        unitPrice: Math.floor(value / area),
      });
      return;
    }
    this.setState({
      [name]: value,
    });
  }

  handleNumberInputChange(e) {
    console.group('num input');
    console.log(e);
    console.log(this.state);
    console.groupEnd();
  }

  toggleCollapse(e, name) {
    e.preventDefault();
    this.setState((prevState) => {
      const { [name]: prevValue } = prevState;
      return {
        [name]: !prevValue,
      };
    });
  }

  handleInputMoreInfoChange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({
      moreInfo: { [name]: value },
    });
    console.log(e.target.value);
    setTimeout(() => {
      const { [name]: value1 } = this.state.moreInfo;
      console.log(name);
      console.log(value1);
    }, 2000);
  }


  handleToggleChange(e) {
    const { name } = e.target;
    this.setState((prevState) => {
      const { [name]: prevValue, ...other } = prevState.moreInfo;
      const newInfo = { ...other, [name]: !prevValue };
      return {
        moreInfo: newInfo,
      };
    });
  }

  handleSelectType(selectedScopes, name) {
    if (selectedScopes === null) {
      this.setState((prevState) => {
        const { [name]: values, ...other } = prevState.tools;
        const newInfo = { ...other, [name]: [] };
        return {
          tools: newInfo,
        };
      });
      return;
    }
    this.setState((prevState) => {
      const { [name]: prevValue, ...other } = prevState.tools;
      const newInfo = { ...other, [name]: selectedScopes };
      return {
        tools: newInfo,
      };
    });
  }

  handleMapClick(map, data) {
    this.setState({
      location: {
        lat: data.lngLat.lng,
        lng: data.lngLat.lat,
      },
    });
  }

  handleSave(e) {
    e.preventDefault();
    console.group('save click');
    console.log(this.state);
    console.groupEnd();
    this.setState({ isLoading: true });
    setTimeout(() => {
      this.setState({ isLoading: false });
    }, 2000);
  }

  render() {
    const checkItems = checkboxItems.map(item => (
      <Col md={2} sm={3} xs={4}>
        <FormGroup>
          <CustomInput
            type="checkbox"
            id={item.name}
            name={item.name}
            label={item.label}
            value={() => {
              const { [item.name]: value } = this.state.moreInfo;
              return {
                value,
              };
            }}
            onChange={this.handleToggleChange}
          />
        </FormGroup>
      </Col>
    ));

    const selectItems = selectNumItems.map(item => (
      <Col md={2} sm={3} xs={6}>
        <FormGroup>
          {renderSelectField({
            input: {
              onChange: (e) => { this.handleTypeSelect(Number(e.value), item.name); },
              isMulti: false,
              name: item.name,
              value: nums[() => {
                const { [item.name]: value } = this.state.moreInfo;
                return {
                  value,
                };
              }],
            },
            placeholder: item.label,
            options: nums,
            name: 'select',
            type: 'text',
          })}
        </FormGroup>
      </Col>
    ));

    const selectTools = selectToolsItems.map(item => (
      <Col md={3} sm={4} xs={6}>
        <FormGroup>
          {renderSelectField({
            input: {
              onChange: (e) => {
                this.handleSelectType(e, item.name);
              },
              isMulti: true,
              name: item.name,
              value: item.values[() => {
                const { [item.name]: value } = this.state.tools;
                return {
                  value,
                };
              }],
            },
            placeholder: item.label,
            options: item.values,
            name: 'select',
            type: 'text',
          })}
        </FormGroup>
      </Col>
    ));

    const otherSelect = selectOtherItems.map(item => (
      <Col md={3} sm={4} xs={6}>
        <FormGroup>
          {renderSelectField({
            input: {
              onChange: (e) => {
                this.handleSelectType(e, item.name);
              },
              isMulti: item.isMulti,
              name: item.name,
              value: item.values[() => {
                const { [item.name]: value } = this.state.otherSelection;
                return {
                  value,
                };
              }],
            },
            placeholder: item.label,
            options: item.values,
            name: 'select',
            type: 'text',
          })}
        </FormGroup>
      </Col>
    ));

    return (
      <div className="newPropertyForm">
        {this.state.isLoading
        && (
          <div className={`load${this.state.isLoading ? '' : ' loaded'}`}>
            <div className="load__icon-wrap">
              <svg className="load__icon">
                <path fill="#4ce1b6" d="M12,4V2A10,10 0 0,0 2,12H4A8,8 0 0,1 12,4Z" />
              </svg>
            </div>
          </div>
        )
        }
        <Form>
          <Row form>
            <Col md={6} sm={6} xs={12}>
              <FormGroup>
                {renderSelectField({
                  input: {
                    onChange: (e) => { this.handleTypeSelect(Number(e.value), 'type'); },
                    isMulti: false,
                    name: 'type',
                    value: types[this.state.type],
                  },
                  placeholder: ' ',
                  options: types,
                  name: 'select',
                  type: 'text',
                })}
              </FormGroup>
            </Col>
            <Col md={6} sm={6} xs={12}>
              <FormGroup>
                {renderSelectField({
                  input: {
                    onChange: (e) => { this.handleTypeSelect(Number(e.value), 'applicationType'); },
                    isMulti: false,
                    name: 'applicationType',
                    value: applicationTypes[this.state.applicationType],
                  },
                  placeholder: '  ',
                  options: applicationTypes,
                  name: 'select',
                  type: 'text',
                })}
              </FormGroup>
            </Col>
            {this.state.type === 4
            && (
              <Col md={4} sm={6} xs={12}>
                <FormGroup>
                  {renderSelectField({
                    input: {
                      onChange: (e) => { this.handleTypeSelect(Number(e.value), 'ownerType'); },
                      isMulti: false,
                      name: 'ownerType',
                      value: ownerTypes[this.state.ownerType],
                    },
                    placeholder: ' ',
                    options: ownerTypes,
                    name: 'select',
                    type: 'text',
                  })}
                </FormGroup>
              </Col>
            )}
            {(this.state.type !== 1 && this.state.type !== 2)
            && (
              <Col md={4} sm={6} xs={12}>
                <FormGroup>
                  <Label for="price"> ()</Label>
                  <DecimalInput
                    type="number"
                    name="price"
                    id="price"
                    placeholder=""
                    value={this.state.price}
                    onChange={(e) => { this.handleInputChange(e); }}
                    className="form-control"
                  />
                </FormGroup>
              </Col>
            )}
            {this.state.type === 1
            && (
              <>
                <Col md={4} sm={6} xs={12}>
                  <FormGroup>
                    <Label for="deposit">()</Label>
                    <DecimalInput
                      type="number"
                      name="deposit"
                      id="deposit"
                      placeholder=""
                      value={this.state.deposit}
                      onChange={(e) => { this.handleInputChange(e); }}
                      className="form-control"
                    />
                  </FormGroup>
                </Col>
                <Col md={4} sm={6} xs={12}>
                  <FormGroup>
                    <Label for="rent">()</Label>
                    <DecimalInput
                      type="number"
                      name="rent"
                      id="rent"
                      placeholder=""
                      value={this.state.rent}
                      onChange={(e) => { this.handleInputChange(e); }}
                      className="form-control"
                    />
                  </FormGroup>
                </Col>
              </>
            )}
            {this.state.type === 2
            && (
              <>
                <Col md={4} sm={6} xs={12}>
                  <FormGroup>
                    <Label for="deposit"> ()</Label>
                    <DecimalInput
                      type="number"
                      name="dailyRent"
                      id="dailyRent"
                      placeholder=" "
                      value={this.state.dailyRent}
                      onChange={(e) => { this.handleInputChange(e); }}
                      className="form-control"
                    />
                  </FormGroup>
                </Col>
              </>
            )}
            <Col md={4} sm={6} xs={12}>
              <FormGroup>
                <Label for="area"></Label>
                <DecimalInput
                  type="number"
                  name="area"
                  id="area"
                  placeholder=""
                  value={this.state.area}
                  onChange={(e) => { this.handleInputChange(e); }}
                  className="form-control"
                />
              </FormGroup>
            </Col>
            {(this.state.type !== 1 && this.state.type !== 2)
            && (
              <Col md={4} sm={6} xs={12}>
                <FormGroup>
                  <Label for="unitPrice">  </Label>
                  <DecimalInput
                    type="number"
                    name="unitPrice"
                    id="unitPrice"
                    placeholder="  "
                    disabled
                    value={this.state.unitPrice}
                    onChange={(e) => { this.handleInputChange(e); }}
                    className="form-control"
                  />
                </FormGroup>
              </Col>
            )}
          </Row>
          <Row>
            <Col xs={12} md={4} sm={6}>
              <Button
                outline
                color="primary"
                onClick={(e) => { this.toggleCollapse(e, 'toggleMoreInfo'); }}
                style={{ marginBottom: '1rem', width: '100%' }}
              >
                   ()
              </Button>
            </Col>
          </Row>
          <Collapse
            isOpen={this.state.toggleMoreInfo}
          >
            <Row form>
              {selectItems}
              {checkItems}
            </Row>
          </Collapse>
          <Row>
            <Col xs={12} md={4} sm={6}>
              <Button
                outline
                color="primary"
                onClick={(e) => { this.toggleCollapse(e, 'toggleTools'); }}
                style={{ marginBottom: '1rem', width: '100%' }}
              >
                   ()
              </Button>
            </Col>
          </Row>
          <Collapse
            isOpen={this.state.toggleTools}
          >
            <Row form>
              {selectTools}
            </Row>
          </Collapse>
          <Row>
            <Col xs={12} md={4} sm={6}>
              <Button
                outline
                color="primary"
                onClick={(e) => { this.toggleCollapse(e, 'toggleOthers'); }}
                style={{ marginBottom: '1rem', width: '100%' }}
              >
                 
              </Button>
            </Col>
          </Row>
          <Collapse
            isOpen={this.state.toggleOthers}
          >
            <Row form>
              {otherSelect}
            </Row>
          </Collapse>
          <Row form>
            <Col sm={12} md={12} lg={12}>
              <ImageUploader
                withIcon
                buttonText=" "
                onChange={(images, pictures) => { this.onDrop(images, pictures); }}
                imgExtension={['.jpg', '.jpeg', '.gif', '.png', '.gif']}
                maxFileSize={5242880}
                withPreview
                fileSizeError="        "
                fileTypeError="     "
                label="         "
              />
            </Col>
          </Row>
          <Row form>
            <Col md={6} sm={12} xs={12} lg={6}>
              <FormGroup>
                <Label for="address"></Label>
                <Input type="textarea" name="address" id="address" />
              </FormGroup>
            </Col>
            <Col md={6} sm={12} xs={12} lg={6}>
              <FormGroup>
                <Label for="description"></Label>
                <Input type="textarea" name="description" id="description" />
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col xs={12} md={4} sm={6}>
              <Button
                outline
                color="primary"
                onClick={(e) => { this.toggleCollapse(e, 'toggleMap'); }}
                style={{ marginBottom: '1rem', width: '100%' }}
              >
                   
              </Button>
            </Col>
          </Row>
          {this.state.toggleMap
          && (
            <div>
              <CedarMaps
                containerStyle={{
                  height: '80vh',
                  width: '100%',
                }}
                token="8d8be29d01ea833ea7bacdd1836567d67c678a70"
                center={[this.state.location.lat, this.state.location.lng]}
                preserveDrawingBuffer={false}
                onClick={(map, data) => { this.handleMapClick(map, data); }}
              >
                <ZoomControl />
                <ScaleControl />
                <Marker
                  coordinates={[this.state.location.lat, this.state.location.lng]}
                >
                  <img src="http://maps.google.com/mapfiles/ms/icons/blue-dot.png" alt="marker" />
                </Marker>
              </CedarMaps>
            </div>
          )}
          <Row>
            <Col lg={4} md={4} sm={4} />
            <Col lg={4} md={4} sm={4}>
              <Button onClick={this.handleSave} className="btn-success btn-save"></Button>
            </Col>
            <Col lg={4} md={4} sm={4} />
          </Row>
        </Form>
      </div>
    );
  }
}

export default AddNewPropertyForm;
